.navBar {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    background-color: #333;
    padding: 10px 0;
}

.navBar a {
    font-style: normal;
    text-decoration: none;
    color: #fff;
    padding: 10px;
    margin: 5px;
    border-radius: 5px;
    transition: background-color 0.3s;
}

.navBar a:hover {
    background-color: rgb(243, 60, 60);
    color: #333;
}

.navBar h2 {
    text-align: center;
    margin: 0;
}
